'use strict';


import client from '../../../helpers/ApiClient';
import updateById from '../../../helpers/updateById';
import findById from '../../../helpers/findById';
import deleteById from '../../../helpers/deleteById';
import ACTION_TYPES from '../../../actionTypes/users';

/*  This is a thunk, meaning it is a function that immediately
 returns a function for lazy evaluation. It is incredibly useful for
 creating async actions, especially when combined with redux-thunk! */

const fetchAll = () => {
  return (dispatch, getState) => {

    dispatch({
      type: ACTION_TYPES.REQUESTING_USERS
    });

    return client.get('/users?filter[include]=userProfile')
      .then(users => {
        dispatch({
          type: ACTION_TYPES.RECEIVED_USERS,
          users
        });
      }).catch(err => {
        dispatch({
          type: ACTION_TYPES.UNAUTHORIZED,
          err
        });
      });
  }
}

const setUserEnabled = (userId, enabled) => (dispatch, getState) => {
  dispatch({
    type: ACTION_TYPES.SAVING_USER_UPDATES,
    userId
  });

  let req = client.put(`/users/${userId}/userProfile`, {
    data : {enabled}
  }).then(userProfile => {
    let oldUserData = findById(userId, getState().users.items);
    let userData = Object.assign({}, oldUserData, {
      userProfile
    });

    dispatch({
      type: ACTION_TYPES.SAVED_USER_UPDATES,
      userId,
      userData
    })
  }).catch(err => {
    dispatch({
      type: ACTION_TYPES.SAVED_USER_UPDATES_ERROR,
      userId,
      err
    })
  });
};

const editUser = (userData) => (dispatch) => {
  dispatch({
    type: ACTION_TYPES.EDIT_USER,
    userData
  })
};

const deleteUser = (userId) => (dispatch, getState) => {
  console.log('called deleteUser', userId);
  dispatch({
    type: ACTION_TYPES.DELETING_USER,
    userId
  })

  let req = client.del(`/users/${userId}`).then(() => {
    dispatch({
      type: ACTION_TYPES.DELETED_USER,
      userId,
    })
  }).catch(err => {
    dispatch({
      type: ACTION_TYPES.DELETED_USER_ERROR,
      userId,
      err
    })
  });

};

const enterEdit = (userId) => (dispatch) => {
  dispatch({
    type: ACTION_TYPES.START_EDITING_USER,
    userId
  });
};

const exitEdit = () => (dispatch) => {
  dispatch({
    type: ACTION_TYPES.EXIT_EDITING_USER
  });
};

const saveEdit = (userId) => (dispatch, getState) => {

  // TODO : save
  dispatch({
    type: ACTION_TYPES.SAVING_USER_UPDATES,
    userId
  })

  let {items} = getState().users;
  // Get the data - then
  let userData = findById(userId, items); // Get a copy
  delete userData.isDirty;

  // TODO


};

export const actions = {
  fetchAll,

  setUserEnabled,
  editUser,
  deleteUser,

  enterEdit,
  exitEdit,
  saveEdit
}

const DEFAULT_STATE = {
  hasFetchedUsers: false,
  isFetchingUsers: false,
  isAuthenticated: true,
  beingEdited: null,
  updatingUserId : false,
  updatedUserId : false,
  items: []
}

export default function usersReducer(state = DEFAULT_STATE, action) {
  switch (action.type) {
    case ACTION_TYPES.UNAUTHORIZED:
      return Object.assign({}, state, {
        isAuthenticated: false
      });

    case ACTION_TYPES.REQUESTING_USERS:
      return Object.assign({}, state, {
        isFetchingUsers: true
      });

    case ACTION_TYPES.RECEIVED_USERS:
      return Object.assign({}, state, {
        isFetchingUsers: false,
        hasFetchedUsers: true,
        items: action.users
      });

    case ACTION_TYPES.START_EDITING_USER:
      return Object.assign({}, state, {
        beingEdited: action.userId
      });

    case ACTION_TYPES.EDIT_USER:
      return (()=>{
        let userData = Object.assign({}, action.userData, {
          isDirty: true
        });
        let items = updateById(action.userId, state.items, userData);
        return Object.assign({}, state, {
          items
        });
      })();

    case ACTION_TYPES.DELETING_USER:
    case ACTION_TYPES.SAVING_USER_UPDATES:
      return Object.assign({}, state, {
        updatingUserId: action.userId
      });

    case ACTION_TYPES.SAVED_USER_UPDATES:
      return (()=>{
        let items = updateById(action.userId, state.items, action.userData);
        return Object.assign({}, state, {
          updatingUserId: null,
          updatedUserId: action.userId,
          items
        });
      })();

    case ACTION_TYPES.SAVED_USER_UPDATES_ERROR:

      // TODO;
      return;

    case ACTION_TYPES.EXIT_EDITING_USER:
      return Object.assign({}, state, {
        beingEdited: null
      });

    case ACTION_TYPES.DELETED_USER:
      return Object.assign({}, state, {
        items : deleteById(action.userId, state.items)
      });

    case ACTION_TYPES.DELETED_USER_ERROR:
      // TODO
      return;

    default:
      return state
  }
}



