'use strict';

import React from 'react';
import PropTypes from 'prop-types';
import './UserListItem.scss';
import Toggle from 'material-ui/Toggle';

import {ListItem} from 'material-ui/List';


import {grey400, darkBlack, lightBlack} from 'material-ui/styles/colors';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';

const iconButtonElement = (
  <IconButton touch={true}>
    <MoreVertIcon color={grey400} />
  </IconButton>
);

let UsersListItem = (props)=>{
  let {
    userProfile: {enabled},
    onEdit
  } = props;

  let enable_disable = enabled ? 'Disable' : 'Enable';
  let toggleEnabled = ()=>{
    props.setUserEnabled(!enabled);
  };

  const rightIconMenu = (
    //<IconMenu iconButtonElement={iconButtonElement}>
    //  <MenuItem onTouchTap={onEdit}>Edit</MenuItem>
    //  <MenuItem onTouchTap={toggleEnabled}>{enable_disable}</MenuItem>
    //  <MenuItem>Delete</MenuItem>
    //</IconMenu>
    // TODO: Fix Edit User
    <IconMenu iconButtonElement={iconButtonElement}>
      <MenuItem onTouchTap={toggleEnabled}>{enable_disable}</MenuItem>
      <MenuItem onTouchTap={props.onDelete}>Delete</MenuItem>
    </IconMenu>
  );

  return <ListItem primaryText={props.email} rightIconButton={rightIconMenu}></ListItem>;
};

UsersListItem.propTypes = {
  setUserEnabled : PropTypes.func.isRequired,
  email: PropTypes.string,
}

export default UsersListItem;




