
// Returns copy of arr, with updated copy of found item
const updateById = (id, arr, data)=>{
  let result = arr.map( (o, i) => {
    if (o.id !== id) {
      return o;
    }

    return Object.assign({}, o, data);
  });

  return result;
}

export default updateById;


