'use strict';


import React, {Component} from 'react';
import PropTypes from 'prop-types'

import './EditUserComponent.scss';
import Toggle from 'material-ui/Toggle';
import OrgSelect from '../../Organizations/components/Select';
import UserRolesList from './UserRolesList';

let EditUserComponent = (props) => {

  let {id, email, userProfile: {enabled, roles, organizationId}} = props.user;
  let label = enabled ? 'Enabled' : 'Disabled';
  let toggleEnabled = () => {
    props.onChange('userProfile', {enabled:!enabled})
  };

  let onChangeOrg = (evt, organizationId)=>{
    props.onChange('userProfile', {organizationId});
  }


  return <div className="edit-user-component">
    <Toggle labelPosition="right" label={label} onToggle={toggleEnabled} toggled={enabled} />
    id: {id}<br/>
    Email: {email}<br/>
    Organization: <OrgSelect onChange={onChangeOrg} selectedId={organizationId} organizations={props.organizations} />

    <paper>
      Roles: <UserRolesList selected={roles} roles={props.roles} />
    </paper>

  </div>
};

EditUserComponent.propTypes = {
  onChange: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  organizations: PropTypes.array.isRequired,
  roles: PropTypes.array.isRequired
};

export default EditUserComponent;



