'use strict';

import React, { Component } from 'react';
import { connect } from 'react-redux'
import { actions } from '../modules/users'
import { actions as roleActions } from '../../Roles/modules/roles';
import { actions as orgActions } from '../../Organizations/modules/organizations';

import PropTypes from 'prop-types'
import FlatButton from 'material-ui/FlatButton'
import Dialog from 'material-ui/Dialog'
import findById from '../../../helpers/findById'
import _set from 'lodash/set';

import EditUserComponent from '../components/EditUserComponent';

const {
  setUserEnabled,
  editUser,
  deleteUser,
  enterEdit,
  exitEdit,
  saveEdit
} = actions;


const mapDispatchToProps = {
  setUserEnabled,
  editUser,
  deleteUser,
  enterEdit,
  exitEdit,
  saveEdit,
  getRoles: roleActions.getRoles,
  getOrganizations: orgActions.getItems
};

const mapStateToProps = (state) => {

  return {
    ...state.users,
    roles: state.roles.roles,
    isRequestingRoles: state.roles.isRequesting,
    hasReceivedRoles: state.roles.hasReceived,
    rolesErrors : state.roles.errors,
    organizations: state.organizations.items,
    hasOrganizations: state.organizations.hasReceived
  }
};

/*  Note: mapStateToProps is where you should use `reselect` to create selectors, ie:

 import { createSelector } from 'reselect'
 const counter = (state) => state.counter
 const tripleCount = createSelector(counter, (count) => count * 3)
 const mapStateToProps = (state) => ({
 counter: tripleCount(state)
 })

 Selectors can compute derived data, allowing Redux to store the minimal possible state.
 Selectors are efficient. A selector is not recomputed unless one of its arguments change.
 Selectors are composable. They can be used as input to other selectors.
 https://github.com/reactjs/reselect    */

class EditUserContainer extends Component {
  static propTypes = {
    // TODO
  };

  componentDidMount() {
    if (!this.props.roles.isRequesting && !this.props.roles.hasReceived) {
      this.props.getRoles();
    }

    if (!this.props.hasOrganizations) {
      this.props.getOrganizations();
    }
  }

  onChange =(propName, val) =>{
    let changed = {...this.props.user};
    _set(changed, propName, val);

    this.props.editUser(changed);
  };

  render () {
    if (!this.props.beingEdited) {
      return false;
    }

    let userBeingEdited = findById(this.props.beingEdited, this.props.items);
    if (!userBeingEdited){
      return false;
    }

    if (!this.props.hasOrganizations || !this.props.hasReceivedRoles) {
      return <Dialog open={true}
                     modal={true}
      >
        <span>Loading</span>
      </Dialog>;
    }

    let disabledSave = (typeof this.props.updatingUserId !== undefined);
    let dialogOpen = (!!userBeingEdited);

    const actions = [
      <FlatButton
        label="Save"
        disabled={disabledSave}
        primary={true}
        onTouchTap={this.props.saveEdit}
        keyboardFocused={true}
      />,
      <FlatButton
        label="Close"
        secondary={true}
        onTouchTap={this.props.exitEdit}
      />
    ];

    return <Dialog open={dialogOpen}
            modal={false}
            actions={actions}
            onRequestClose={this.props.exitEdit}
            autoScrollBodyContent={true}
          >
            <EditUserComponent user={userBeingEdited} roles={this.props.roles} organizations={this.props.organizations} onChange={this.onChange} />
          </Dialog>
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(EditUserContainer)


