
import React from 'react';
import PropTypes from 'prop-types'
import './UsersListView.scss'
import UserListItem from './UserListItem'

import {List} from 'material-ui/List';

let UsersListView = (props) => (

  <div className="user-list-view">
    <List>
        {props.users.map((user, i) => {
          let {id} = user;
          let setUserEnabled = props.setUserEnabled.bind(null, id);
          let enterEdit = props.enterEdit.bind(null, id);
          let onDelete = props.deleteUser.bind(null, id);
          return <UserListItem {...user} key={id} setUserEnabled={setUserEnabled} onEdit={enterEdit} onDelete={onDelete} />
        })}
    </List>
  </div>
);

UsersListView.propTypes = {
  users: PropTypes.array.isRequired,
  hasFetchedUsers: PropTypes.bool,
  isFetchingUsers: PropTypes.bool,
  setUserEnabled : PropTypes.func.isRequired,
  deleteUser : PropTypes.func.isRequired
}

export default UsersListView;




