'use strict';

import client from '../../../helpers/ApiClient';
import _pick from 'lodash/pick';
import upsertById from '../../../helpers/upsertById';


// ------------------------------------
// Constants
// ------------------------------------
import ACTION_TYPES from '../../../actionTypes/roles';

const DEFAULT_STATE = {
  roles: [],
  isRequesting: false,
  hasReceived: false,
  errors: false
};


/*  This is a thunk, meaning it is a function that immediately
 returns a function for lazy evaluation. It is incredibly useful for
 creating async actions, especially when combined with redux-thunk! */

const getRoles = () => {
  return (dispatch, getState) => {

    dispatch({
      type: ACTION_TYPES.REQUESTING_ROLES
    });

    return client.get('/roles?filter[order]=created DESC')
      .then(roles => {
        dispatch({
          type: ACTION_TYPES.RECEIVED_ROLES,
          roles

        });
      }).catch(errors => {
        dispatch({
          type: ACTION_TYPES.RECEIVED_ROLES_ERROR,
          errors
        });
      });
  }
}

const selectRoles = function(roleIds){
  return (dispatch, getState) => {
    return dispatch({
      type: ACTION_TYPES.ONCHANGE_SELECTED_ROLES,
      selectedRoleIds : roleIds
    });
  }
}

const deleteRoles = function(roleIds){
  return (dispatch, getState) => {
    dispatch({
      type: ACTION_TYPES.DELETING_ROLES,
      roleIds
    });

    // TODO implement bulk delete
    let where = JSON.stringify({where: { property: { inq: roleIds}}})
    return client.del(`/roles/deleteByIds?ids=${roleIds.join(',')}`)
      .then(result => {
        dispatch({
          type: ACTION_TYPES.DELETED_ROLES,
          roleIds
        });
      }).catch(errors => {
        dispatch({
          type: ACTION_TYPES.RECEIVED_ROLES_ERROR,
          errors
        });
      });
  }
}

const _getUpdatedRole = function(dispatch, actionType, {data:{id}}) {
  return client.get(`/roles/${id}`).then(role=>{
    return dispatch({
      type: actionType,
      role
    });
  });
}

const getDeletedIdsFromMsgData = function(msgData) {
  let {id} = msgData;
  if (typeof id === 'object' && id.inq) {
    return id.inq;
  }

  return [id];
}

const onRoleChangeStreamMsg = function(msg) {
  let data = JSON.parse(msg.data);

  return (dispatch, getState) => {
    switch (data.type) {
      case 'create':
        return _getUpdatedRole(dispatch, ACTION_TYPES.ADDED_ROLE, data);
      case 'update':
        return _getUpdatedRole(dispatch, ACTION_TYPES.UPDATED_ROLE, data);

      case 'remove':
        let roleIds = getDeletedIdsFromMsgData(data)
        return dispatch({
          type: ACTION_TYPES.DELETED_ROLES,
          roleIds
        })

      default:
        break;
    }
  }
}

export const actions = {
  getRoles,
  selectRoles,
  deleteRoles,

  onRoleChangeStreamMsg
}

export default function rolesReducer(state = DEFAULT_STATE, action) {
  let newState = Object.assign({}, state);
  switch(action.type) {
    case ACTION_TYPES.REQUESTING_ROLES:
      return Object.assign({}, state, {
        isRequesting:true,
        errors: false
      });

    case ACTION_TYPES.RECEIVED_ROLES:
      return Object.assign({}, state, {
        isRequesting:false,
        hasReceived: true,
        roles: action.roles
      });

    case ACTION_TYPES.RECEIVED_ROLES_ERROR:
      return Object.assign({}, state, {
        isRequesting:false,
        errors: action.errors
      });

    case ACTION_TYPES.ONCHANGE_SELECTED_ROLES:
      return Object.assign({}, state, {
        selectedRoleIds : action.selectedRoleIds
      });

    case ACTION_TYPES.DELETING_ROLES:
      return Object.assign({}, state, {
        isDeleting: true,
        hasDeleted: false
      });

    case ACTION_TYPES.DELETED_ROLES:
      return (()=> {
        let deletedRolesIds = action.roleIds;
        let oldRoles = state.roles;
        let newRoles = oldRoles.filter((role, i)=> {
          let {id} = role;
          return deletedRolesIds.indexOf(id) === -1;
        });

        return Object.assign({}, state, {
          isDeleting: false,
          hasDeleted: true,
          roles: newRoles,
          selectedRoleIds: []
        });
      })();

    case ACTION_TYPES.DELETED_ROLES_ERROR:
      return Object.assign({}, state, {
        isDeleting: false,
        hasDeleted: false
      });

    // When push is enabled toggle these back
    //case ACTION_TYPES.ADDED_ROLE:
    case ACTION_TYPES.CREATED_ROLE:
      return (()=> {
        // Avoids duplicates
        let roles = upsertById(action.role, state.roles);
        return Object.assign({}, state, {
          roles
        });
      })();

    case ACTION_TYPES.UPDATED_ROLE:
      return (()=> {
        let roles = upsertById(action.role, state.roles);
        return Object.assign({}, state, {
          roles
        });
      })()

    default:
      return state;
  }
};




