'use strict';

import React, {Component} from 'react';
import PropTypes from 'prop-types'
import UserRoleListItem from './UserRoleListItem';
import './UserRolesList.scss';

class UserRolesList extends Component {

  constructor(props){
    super(props);
    this.state = {
      selected: this.props.selected.map(role => role.id)
    }
  }

  isSelected = (id) => {
    let selected = this.state.selected.indexOf(id) !== -1;
    return selected;
  }

  onItemSelect = (id) => (evt, selected) => {
    let selectedIds = [].concat(this.props.selected);
    if (selected) {
      selectedIds.push(id);
    } else {
      selectedIds.splice(selectedIds.indexOf(id), 1);
    }

    this.props.selectRoles(selectedIds);
  }

  render() {
    let {props, onItemSelect} = this;
    return <table className="roles-list">
      <tbody>
      {props.roles.map((role, i) => {
        let {id} = role;
        let selected = this.isSelected(id);
        return <UserRoleListItem
          key={`role-list-item-${id}`}
          {...role}
          selected={selected}
          onSelect={onItemSelect(id)} />
      })}
      </tbody>
    </table>
  }
}

UserRolesList.defaultProps = {
  roles: [],
  selected: []
}

UserRolesList.propTypes = {
  roles: PropTypes.array.isRequired,
  selected : PropTypes.array.isRequired,
  selectRoles : PropTypes.func.isRequired
}

export default UserRolesList;




