import React, { Component } from 'react';
import { connect } from 'react-redux'
import { actions } from '../modules/users'
import PropTypes from 'prop-types'

/*  This is a container component. Notice it does not contain any JSX,
 nor does it import React. This component is **only** responsible for
 wiring in the actions and state necessary to render a presentational
 component - in this case, the counter:   */

import UsersListView from '../components/UsersListView'
import EditUserContainer from './EditUserContainer'

/*  Object of action creators (can also be function that returns object).
 Keys will be passed as props to presentational components. Here we are
 implementing our wrapper around increment; the component doesn't care   */

const mapDispatchToProps = actions;

const mapStateToProps = (state) => {
  let {
    hasFetchedUsers,
    isFetchingUsers,
    beingEdited,
    updatingUserId,
    updatedUserId
  } = state.users;

  return {
    users : state.users.items,
    roles : state.roles.roles,
    hasFetchedUsers,
    isFetchingUsers
  }
};

/*  Note: mapStateToProps is where you should use `reselect` to create selectors, ie:

 import { createSelector } from 'reselect'
 const counter = (state) => state.counter
 const tripleCount = createSelector(counter, (count) => count * 3)
 const mapStateToProps = (state) => ({
 counter: tripleCount(state)
 })

 Selectors can compute derived data, allowing Redux to store the minimal possible state.
 Selectors are efficient. A selector is not recomputed unless one of its arguments change.
 Selectors are composable. They can be used as input to other selectors.
 https://github.com/reactjs/reselect    */

class RegisteredUsersListContainier extends Component {
  static propTypes = {
    // List
    roles: PropTypes.array.isRequired,
    users: PropTypes.array.isRequired,
    hasFetchedUsers: PropTypes.bool.isRequired,
    isFetchingUsers: PropTypes.bool.isRequired,
    fetchAll : PropTypes.func.isRequired,

    // Edit
    beingEdited : PropTypes.object,
    updatingUserId: PropTypes.string,
    updatedUserId: PropTypes.string,

    deleteUser : PropTypes.func.isRequired,
    enterEdit: PropTypes.func.isRequired,
    exitEdit : PropTypes.func.isRequired,
    saveEdit : PropTypes.func.isRequired,
    setUserEnabled: PropTypes.func.isRequired
  };

  componentDidMount () {
    if (!this.props.hasFetchedUsers) {
      this.props.fetchAll();
    }
  }

  render () {
    return <div>
      <UsersListView {...this.props} />
      <EditUserContainer />
    </div>
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RegisteredUsersListContainier)


